import { isPlatformBrowser } from '@angular/common';
import { Component, Inject, Input, OnInit, PLATFORM_ID } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { AuthService } from '@core/data-access/services/auth.service';
import { AisleType, PostLoginAction, StorageType } from '@core/enums';
import { CommonFacade } from '@core/facades/common.facade';
import { Ad, Translations } from '@core/models';
import { BrowserUtility } from '@core/services/browser-utility.service';
import { IconService } from '@core/services/icon.service';
import { SsoPostLoginService } from '@core/services/sso-post-login.service';
import { ViewportService } from '@core/services/viewport.service';
import { WindowService } from '@core/services/window.service';
import { PropertyAlertDialogComponent } from '@feature/general/filters/components/property-alert-dialog/property-alert-dialog.component';
import { AdReportDialogComponent } from '@shared/components/ads/ad-report-dialog/ad-report-dialog.component';
import { ButtonType } from '@shared/components/button/button.component';
import { SharedAdsFacade } from '@shared/facades/shared-ads.facade';
import dayjs from 'dayjs';
import relativeTime from 'dayjs/plugin/relativeTime';
import { AdShareDialogComponent } from '../../../../shared/components/ads/ad-share-dialog/ad-share-dialog.component';

interface BadgeProperty {
	property?: string;
	image?: string;
	name?: string;
	icon?: string;
	iconClass?: string;
	property2?: string;
}

// TODO: Fix duplicate codes for report ad, share ad and add to favorites written in AdHeaderComponent and AdMainHeadingsComponent.
@Component({
    selector: 'findqo-ad-main-headings',
    templateUrl: './ad-main-headings.component.html',
    styleUrls: ['./ad-main-headings.component.scss'],
    standalone: false
})
export class AdMainHeadingsComponent implements OnInit {
	constructor(
		@Inject(PLATFORM_ID) private readonly platformId: string,
		public readonly icon: IconService,
		public readonly viewport: ViewportService,
		private readonly matDialog: MatDialog,
		private readonly auth: AuthService,
		private readonly commonFacade: CommonFacade,
		private readonly sharedAdsFacade: SharedAdsFacade,
		private readonly window: WindowService,
		private readonly utility: BrowserUtility,
		private authService: AuthService,
		private readonly dialog: MatDialog,
		private readonly ssoPostLoginService: SsoPostLoginService
	) {}

	@Input() translations: Translations;
	@Input() ad: Ad;
	@Input() isPublicPage = false;

	user$ = this.commonFacade.getUser$();
	datePosted: string;
	readonly aisleType = AisleType;

	readonly buttonType = ButtonType;
	readonly badgeMotors: BadgeProperty[] = [
		{ property: 'kilometer', image: 'kilometers.svg', name: 'n304' },
		{ property: 'regionalSpec', image: 'region.svg', name: 'n425' },
		{ property: 'isWarranty', image: 'warranty.svg', name: 'n171' },
		{ property: 'year', image: 'year.svg', name: 'n161' },
		{ property: 'color', image: 'color.svg', name: 'n178' },
	];
	readonly badgeMotorCycle: BadgeProperty[] = [
		{ property: 'kilometer', image: 'kilometers.svg', name: 'n304' },
		{ property: 'engineSizeCc', image: 'engine.svg', name: 'n722' },
		{ property: 'color', image: 'color.svg', name: 'n178' },
	];

	readonly badgeProperties: BadgeProperty[] = [
		{ property: 'bedroomCount', icon: 'bedroom', iconClass: 'material-icons-outlined', name: 'n347' },
		{ property: 'bathroomCount', icon: 'shower', name: 'n659' },
		{ property: 'propertySize', property2: 'value', icon: 'select_all' },
	];

	shareAd(): void {
		const TRANSLATIONS = this.commonFacade.getTranslations();
		const AVALABLE_FROM = this.ad.availableFrom ? TRANSLATIONS?.['n3111'] + ': ' + this.commonFacade.getFormattedDate(this.ad.availableFrom) : '';

		// Sharing ad from Ad detail page
		if (this.utility.isWebShareSupported()) {
			navigator
				.share({
					title: this.ad?.title,
					text: `${this.ad?.title}\n${TRANSLATIONS?.['n152']}:  ${+this.ad.price.isPriceOnApplication ? TRANSLATIONS?.['n3311'] : this.ad.price.symbol + this.ad.price.minValue + ' ' + (this.ad.price.maxValue ? ' - ' + this.ad.price.maxValue : '')}\n${AVALABLE_FROM}\n${TRANSLATIONS?.['n3953']}\n`,
					url: this.ad?.adlink ?? this.window.reference.location.href,
				})
				.then(() => {
					// TODO: Do we need snackbar here?
					console.log('Content shared successfully!');
				})
				.catch((error) => {
					console.error('Error sharing content:', error);
				});
		} else {
			this.matDialog
				.open(AdShareDialogComponent, {
					disableClose: true,
					maxWidth: '407px',
					data: { adLink: this.ad.adlink },
				})
				.afterClosed();
		}
	}

	isFavoritesBtnClicked(isFavoritesBtnEnable: boolean): void {
		if (isFavoritesBtnEnable) {
			this.sharedAdsFacade.deleteFavouriteAd(this.ad);
		} else {
			this.saveFavorites();
		}
	}

	private addToFav(): void {
		if (this.auth.user()?.id !== this.ad.seller.id) {
			this.ad = {
				...this.ad,
				isFavorite: '1',
			};
			this.sharedAdsFacade.saveFavouriteAd(this.ad);
		}
	}

	private saveFavorites(): void {
		const IS_LOGGED_IN = this.auth.user()?.accessToken;

		if (IS_LOGGED_IN) {
			this.addToFav();
		} else {
			sessionStorage.setItem(
				StorageType.POST_LOGIN_SESSION,
				JSON.stringify({
					type: PostLoginAction.ADD_TO_FAVORITE,
				})
			);

			this.auth.openAuthDialog({ onLogin: this.addToFav.bind(this) });
		}
	}

	onReportAd(): void {
		if (this.auth.user()) {
			this.openReportDialog();
		} else {
			sessionStorage.setItem(
				StorageType.POST_LOGIN_SESSION,
				JSON.stringify({
					type: PostLoginAction.REPORT_AD,
				})
			);
			this.auth.openAuthDialog({ onLogin: this.openReportDialog.bind(this) });
		}
	}

	private openReportDialog(): void {
		this.matDialog.open(AdReportDialogComponent, {
			width: '343px',
			height: 'auto',
			maxWidth: 'unset',
			data: {
				adId: this.ad?.id,
			},
			backdropClass: 'inquiry-backdrop',
		});
	}

	onShowAlertsDialog() {
		if (!this.authService.user()) {
			this.authService.openAuthDialog();
			return;
		}

		this.dialog.open(PropertyAlertDialogComponent, {
			disableClose: true,
			panelClass: 'property-alert-dialog',
			data: { section: this.ad?.section?.reference, aisle: this.ad?.aisle?.reference, location: this.ad?.location },
		});
	}

	ngOnInit(): void {
		if (isPlatformBrowser(this.platformId)) {
			this.ssoPostLoginService.handlePostLoginWithAction({
				[PostLoginAction.ADD_TO_FAVORITE]: this.addToFav.bind(this),
				[PostLoginAction.REPORT_AD]: this.openReportDialog.bind(this),
			});
		}

		dayjs.extend(relativeTime);
		this.datePosted = dayjs(+this.ad?.createdAt * 1000).fromNow();
	}
}
