import { Component, Input, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { APP_ROUTE } from '@core/constants';
import { AdsApi } from '@core/data-access/apis/ads.api';
import { AmplitudeEvent, SectionType, SellerType } from '@core/enums';
import { CommonFacade } from '@core/facades/common.facade';
import { Ad } from '@core/models';
import { EnquiryService } from '@core/services/enquiry.service';
import { SnackService } from '@core/services/snack.service';
import { ViewportService } from '@core/services/viewport.service';
import { environment } from '@environments/environment';
import { SendInquiryDialogComponent } from '@feature/general/send-inquiry/send-inquiry-dialog.component';
import { ButtonType } from '@shared/components/button/button.component';
import { SharedAdsFacade } from '@shared/facades/shared-ads.facade';
import { AdsState } from '@shared/states/ads.state';

@Component({
    selector: 'findqo-ad-contacts-v2',
    templateUrl: './ad-contacts-v2.component.html',
    styleUrls: ['./ad-contacts-v2.component.scss'],
    standalone: false
})
export class AdContactsV2Component implements OnInit {
	constructor(
		public readonly viewport: ViewportService,
		private readonly matDialog: MatDialog,
		private readonly commonFacade: CommonFacade,
		private readonly adsApi: AdsApi,
		private readonly adsState: AdsState,
		private readonly sharedAdsFacade: SharedAdsFacade,
		private readonly enquiryService: EnquiryService,
		private snackService: SnackService,
	) {}

	@Input() ad: Ad;
	@Input() isPublicPage = false;
	@Input() showPrice: boolean;

	inquiryData: any;
	user$ = this.commonFacade.getUser$();
	defaultAvatarImg = environment.defaultAvatarImg;
	isBussinessAccount: boolean;
	readonly sectionType = SectionType;
	readonly buttonType = ButtonType;
	readonly appRoute = APP_ROUTE;
	isPhoneClicked = false;
	readonly isProduction = environment.production;
	readonly defaultCountryCode = '+353';

	getMessage(status: string) {
		if (status === 'sold') {
			return 'This property is sold';
		} else if (status === 'property-occupied') {
			return 'This property is occupied';
		} else if (status === 'sale-agreed') {
			return 'This property is sale agreed';
		} else {
			return '';
		}
	}

	onSendInquiry(status: string): void {
		if (['sold', 'property-occupied', 'sale-agreed', 'expired'].includes(status) && this.viewport.desktop) {
			return null;
		}
		if (['sold', 'property-occupied', 'sale-agreed', 'expired'].includes(status) && !this.viewport.desktop) {
			this.snackService.open({
				data: { message: this.getMessage(status) },
				panelClass: 'snackbar-error',
				duration: 3000,
			});
			return;
		}
		this.enquiryService.onOpenSendEnquiryDialog(AmplitudeEvent.AD_DETAILS_SEND_ENQUIRY_CLICKED, () => this.openSendInquiryDialog());
	}

	private openSendInquiryDialog(): void {
		this.matDialog
			.open(SendInquiryDialogComponent, {
				width: '100%',
				height: '100%',
				position: {
					right: '0',
				},
				data: {
					ad: this.ad,
					sharedAdsFacade: this.sharedAdsFacade,
				},
				panelClass: 'inquiry-form',
			})
			.afterClosed()
			.subscribe((res) => {
				if (res) {
					this.inquiryData = res?.inquiryData;
					this.adsApi.getAdDetail(this.ad.id).subscribe((ad: Ad) => {
						this.adsState.setAd(ad);
					});
				}
			});
	}

	showPhoneNumber(adId: string): void {
		this.adsApi.trackPhoneCall(adId).subscribe();
		this.isPhoneClicked = true;
	}

	onCall(adId: string): void {
		if (!this.isPhoneClicked) {
			this.adsApi.trackPhoneCall(adId).subscribe();
		}
	}

	ngOnInit(): void {
		this.isPhoneClicked = false;

		this.isBussinessAccount = this.ad?.seller?.sellerTypeId === SellerType.BUSINESS;
	}
}
