import { Clipboard } from '@angular/cdk/clipboard';
import { isPlatformBrowser } from '@angular/common';
import { Component, ElementRef, HostListener, Inject, Input, OnInit, PLATFORM_ID, ViewChild } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { ActivatedRoute } from '@angular/router';
import { BER_EXEMPT } from '@core/constants';
import { AuthService } from '@core/data-access/services/auth.service';
import { FeatureVersion, PostLoginAction, StorageType } from '@core/enums';
import { SectionType } from '@core/enums';
import { CommonFacade } from '@core/facades/common.facade';
import { Ad, Brochure, Translations } from '@core/models';
import { AdTrackingService } from '@core/services/ad-tracking.service';
import { CanonicalService } from '@core/services/canonical.service';
import { RouteService } from '@core/services/route.service';
import { SeoService } from '@core/services/seo.service';
import { SnackService } from '@core/services/snack.service';
import { StructuredDataService } from '@core/services/structured-data.service';
import { ViewportService } from '@core/services/viewport.service';
import { environment } from '@environments/environment';
import { AdDetailFacade } from '@feature/ad-detail/ad-detail.facade';
import { AdReportDialogComponent } from '@shared/components/ads/ad-report-dialog/ad-report-dialog.component';
import { ButtonType } from '@shared/components/button/button.component';
import { SharedAdsFacade } from '@shared/facades/shared-ads.facade';
import { AdsService } from '@shared/services/ads.service';
import { DestroyService } from '@shared/services/destroy.service';
import dayjs from 'dayjs';
import relativeTime from 'dayjs/plugin/relativeTime';
import { Observable, takeUntil } from 'rxjs';

@Component({
    templateUrl: './ad-detail.page.html',
    styleUrls: ['./ad-detail.page.scss'],
    providers: [DestroyService],
    standalone: false
})
export class AdDetailPage implements OnInit {
	constructor(
		@Inject(PLATFORM_ID) private platformId: string,
		private readonly route: ActivatedRoute,
		private readonly adDetailFacade: AdDetailFacade,
		private readonly commonFacade: CommonFacade,
		public readonly viewport: ViewportService,
		private readonly destroyService: DestroyService,
		private readonly adsService: AdsService,
		private readonly seoService: SeoService,
		private readonly canonicalService: CanonicalService,
		private readonly schemaService: StructuredDataService,
		private readonly sharedAdsFacade: SharedAdsFacade,
		private readonly routeService: RouteService,
		private readonly adTracker: AdTrackingService,
		private readonly snackService: SnackService,
		private readonly clipboard: Clipboard,
		private readonly matDialog: MatDialog,
		private readonly auth: AuthService,
	) {
		this.translations$ = this.commonFacade.getTranslations$();
		this.showHeader$ = this.adsService.isDesktopContactsInvisibile$();
		this.relatedAdList$ = this.adDetailFacade.getRelatedAdList$();
		this.ad$ = this.sharedAdsFacade.getAd$();
	}

	@Input() isPublicPage = false;
	translations$: Observable<Translations>;
	showHeader$: Observable<boolean>;
	relatedAdList$: Observable<Ad[]>;
	ad$: Observable<Ad>;
	extras: string[] = [];
	user$ = this.commonFacade.getUser$();

	readonly isProduction = environment.production;
	readonly sectionType = SectionType;
	readonly featureVersion = FeatureVersion;
	readonly buttonType = ButtonType;
	readonly berExempt = BER_EXEMPT;
	recentAds: Ad[];

	@ViewChild('subDetails') subDetails!: ElementRef;
	showFooterContent = false;

	@HostListener('window:scroll', [])
	onWindowScroll() {
		if (!this.subDetails) return;

		const rect = this.subDetails?.nativeElement?.getBoundingClientRect();
		this.showFooterContent = rect?.top < 0;
	}

	onReportAd(ad: Ad): void {
		if (this.auth.user()) {
			this.openReportDialog(ad);
		} else {
			sessionStorage.setItem(
				StorageType.POST_LOGIN_SESSION,
				JSON.stringify({
					type: PostLoginAction.REPORT_AD,
				}),
			);
			this.auth.openAuthDialog({ onLogin: this.openReportDialog.bind(this) });
		}
	}

	private openReportDialog(ad: Ad) {
		this.matDialog.open(AdReportDialogComponent, {
			width: '343px',
			height: 'auto',
			maxWidth: 'unset',
			data: {
				adId: ad?.id,
			},
			backdropClass: 'inquiry-backdrop',
		});
	}

	// TODO: Move this on separate service
	private setMetaInformation(ad: Ad): void {
		let propertyAmenities = [];
		const includedAmenities = [];
		const matchingAmenities = [];

		dayjs.extend(relativeTime);
		const AVAILABLE_FROM = dayjs(+ad?.availableFrom * 1000).fromNow(true);
		const amenities: string[] = ['Great Location', 'Parking', 'Heating', 'Pets Allowed', 'Stylish Bathrooms'];
		const AD_AMENETIES = ad.propertyExtras;

		if (!AD_AMENETIES || AD_AMENETIES.length === 0) {
			propertyAmenities = amenities.slice(0, 5);
		} else {
			// Find matching amenities from adAmenities and amenities list
			for (let i = 0; i < amenities.length; i++) {
				const AMENITY = amenities[i];
				if (AD_AMENETIES.includes(AMENITY) && !includedAmenities.includes(AMENITY)) {
					matchingAmenities.push(AMENITY);
					includedAmenities.push(AMENITY);
				}
			}

			// Add all matching amenities to propertyAmenities
			propertyAmenities = [...matchingAmenities];

			let remainingAmenitiesCount = 5 - propertyAmenities.length;

			if (remainingAmenitiesCount > 0) {
				// Add additional adAmenities to make the total count 5, if available
				for (let i = 0; i < AD_AMENETIES.length && remainingAmenitiesCount > 0; i++) {
					const AMENITY = AD_AMENETIES[i];

					if (!includedAmenities.includes(AMENITY)) {
						propertyAmenities.push(AMENITY);
						includedAmenities.push(AMENITY);
						remainingAmenitiesCount--;
					}
				}
			}
		}

		const PROPERTY_PRICE = ` | Price: ${ad?.price?.symbol}${ad?.price?.minValue}${ad?.price?.maxValue ? ' - ' + ad?.price?.symbol + ad?.price?.maxValue : ''} ${
			ad?.rentalPeriod || ''
		}${+ad?.bedroomCount > 0 ? ` | Beds: ${ad?.bedroomCount}` : ''}${+ad?.bathroomCount > 0 ? ` | Baths: ${ad?.bathroomCount}` : ''}${
			ad?.propertySize && ad?.propertySize?.value !== '0' ? ` | size: ${ad?.propertySize?.value} ${ad?.propertySize?.parameter}` : ''
		}`;

		let propertyDetals = '';

		if (ad?.section?.reference === 'properties-for-sale' && ad?.furnishing) {
			propertyDetals = ` | Furnishing: ${ad.furnishing}`;
		} else {
			propertyDetals = ` | Available from: ${AVAILABLE_FROM} ${ad?.leaseTerm ? ` | Lease Term: ${ad.leaseTerm}` : ''}${ad?.furnishing ? ` | Furnishing: ${ad.furnishing}` : ''}`;
		}

		const AMENITIES = propertyAmenities?.length ? ` | Amenities: ${propertyAmenities.join(', ')}` : '';

		const FORMATTED_DATE = new Date(+ad?.createdAt * 1000).toLocaleDateString('en-IE', {
			year: 'numeric',
			month: 'short',
			day: 'numeric',
		});

		const CREATED_AT = ` | Posted on: ${FORMATTED_DATE}`;
		const AD_ID = ad?.id ? ` | Ad ID: ${ad.id}` : '';

		const EIR_CODE = ad?.location?.eirCode;
		const TITLE = EIR_CODE && !ad?.title?.includes(EIR_CODE) ? `${ad.title}, ${EIR_CODE} | FindQo.ie` : `${ad.title} | FindQo.ie`;

		this.seoService.setSeo({
			title: TITLE,
			description: ad?.title + PROPERTY_PRICE + propertyDetals + AMENITIES + CREATED_AT + AD_ID,
			image: ad?.images[0]?.url,
		});
	}

	onCopyLocation(location: string): void {
		this.clipboard.copy(location);

		this.snackService.open({
			data: { message: 'Address copied!', type: 'success' },
			panelClass: 'snackbar-success',
		});
	}

	// TODO: PDF link will be coming from API, added agent pdf for testing
	onDownloadBrochure(brochure: Brochure): void {
		window.open(brochure?.name, '_blank');
	}

	private setSeo(ad: Ad): void {
		this.canonicalService.createCannonicaUrl(ad.adlink, true);
	}

	ngOnInit(): void {
		this.route.data.pipe(takeUntil(this.destroyService)).subscribe((pageData: { ad: Ad }) => {
			if (pageData.ad) {
				this.adTracker.saveClickedAd(pageData.ad); // we’ll handle the logic inside the service
			}
			this.recentAds = this.adTracker.getRecentAds().filter((a) => a.id !== pageData.ad.id); // exclude current ad from display
			if (pageData.ad) {
				const AD = pageData.ad;

				this.extras = AD?.propertyExtras;

				this.setMetaInformation(AD);
				this.setSeo(AD);

				this.schemaService.createProductSchema(AD, AD.adlink);

				if (isPlatformBrowser(this.platformId)) {
					this.adDetailFacade.loadRelatedAdList(AD);
				}
			} else {
				this.seoService.robotsNoindex();
				this.routeService.setRouteData({ ...this.routeService.getRouteData(), pageReference: 'ad-not-found' });
			}
		});
	}

	ngAfterViewInit() {
		this.onWindowScroll();
	}
}
