import { Component, Input } from '@angular/core';

import { Ad, Translations } from '@core/models';
import { AdsService } from '@shared/services/ads.service';
import { ButtonType } from '@shared/components/button/button.component';
import { Observable } from 'rxjs';

@Component({
    selector: 'findqo-ad-contacts',
    templateUrl: './ad-contacts.component.html',
    styleUrls: ['./ad-contacts.component.scss'],
    standalone: false
})
export class AdContactsComponent {
	constructor(private adsService: AdsService) {
		this.addPrice$ = this.adsService.isTitleInvisible$();
	}

	@Input() ad: Ad;
	@Input() translations: Translations;
	addPrice$: Observable<boolean>;

	readonly buttonType = ButtonType;

	onWhatsappMessage(): void {
		this.adsService.onWhatsappMessage(this.ad);
	}

	onCall(): void {
		this.adsService.onPhoneCall(this.ad);
	}
}
