import { Component, Input, OnInit } from '@angular/core';
import { Ad } from '@core/models';

import { AdsService } from '@shared/services/ads.service';
import { ButtonType } from '@shared/components/button/button.component';
import { ButtonModule } from '@shared/components/button/button.module';
import { SharedModule } from '@shared/shared.module';

@Component({
	selector: 'findqo-ad-summary-header',
	standalone: true,
	imports: [ButtonModule, SharedModule],
	templateUrl: './ad-summary-header.component.html',
	styleUrls: ['./ad-summary-header.component.scss'],
})
export class AdSummaryHeaderComponent implements OnInit {
	constructor(private readonly adsService: AdsService) {}

	@Input() ad: Ad;

	readonly buttonType = ButtonType;

	onWhatsappMessage(): void {
		this.adsService.onWhatsappMessage(this.ad);
	}

	onCall(): void {
		this.adsService.onPhoneCall(this.ad);
	}

	ngOnInit(): void {}
}
