import { Component, Input } from '@angular/core';

import { Ad } from '@core/models';
import { ButtonType } from '@shared/components/button/button.component';
import { AdPhotoViewerComponent } from '../../../../shared/components/ads/ad-photo-viewer/ad-photo-viewer.component';
import { MatDialog } from '@angular/material/dialog';
import { SharedAdsFacade } from '@shared/facades/shared-ads.facade';
import { ButtonModule } from '@shared/components/button/button.module';
import { MatIconModule } from '@angular/material/icon';
import { SharedModule } from '@shared/shared.module';

@Component({
	selector: 'findqo-ad-desktop-photo',
	standalone: true,
	imports: [ButtonModule, MatIconModule, SharedModule],
	templateUrl: './ad-desktop-photo.component.html',
	styleUrls: ['./ad-desktop-photo.component.scss'],
})
export class AdDesktopPhotoComponent {
	constructor(
		public readonly dialog: MatDialog,
		private readonly sharedAdsFacade: SharedAdsFacade
	) {}

	@Input() ad: Ad;

	readonly buttonType = ButtonType;

	openImageGallery(): void {
		this.dialog.open(AdPhotoViewerComponent, {
			width: '100%',
			height: '100%',
			data: {
				ad: this.ad,
				sharedAdsFacade: this.sharedAdsFacade,
			},
			panelClass: 'ad-images',
		});
	}
}
