import { Injectable } from '@angular/core';
import { Ad } from '@core/models/ads.model';

@Injectable({
	providedIn: 'root',
})
export class AdTrackingService {
	private readonly key = 'recentAds';
	private readonly maxAds = 5;

	saveClickedAd(ad: Ad): void {
		let ads: Ad[] = JSON.parse(localStorage.getItem(this.key) || '[]');

		// Remove existing ad by id
		ads = ads.filter((existing) => existing.id !== ad.id);

		// Add new ad
		ads.unshift(ad);

		// Trim to last 4
		ads = ads.slice(0, this.maxAds);

		localStorage.setItem(this.key, JSON.stringify(ads));
	}

	getRecentAds(): Ad[] {
		return JSON.parse(localStorage.getItem(this.key) || '[]');
	}
}
