import { inject } from '@angular/core';
import { ActivatedRouteSnapshot, ResolveFn, RouterStateSnapshot } from '@angular/router';
import { AdsApi } from '@core/data-access/apis/ads.api';
import { Ad } from '@core/models';
import { SnackService } from '@core/services/snack.service';
import { ResponseService } from '@shared/services/response.service';
import { AdsState } from '@shared/states/ads.state';
import { Observable, catchError, finalize, of, tap } from 'rxjs';

export const AdDetailResolver: ResolveFn<any> = (
	route: ActivatedRouteSnapshot,
	_state: RouterStateSnapshot,
	adsApi: AdsApi = inject(AdsApi),
	adsState = inject(AdsState),
	snack = inject(SnackService),
	responseService = inject(ResponseService)
): Observable<any> => {
	adsState.setIsLoading(true);

	return adsApi.getAdDetail(route.params['adId']).pipe(
		catchError((error) => {
			if (error?.error?.error?.id === 'adNotLive') {
				snack.open({
					data: {
						message: 'This listing is not live yet. Please check again later or contact FindQo support',
					},
					panelClass: 'snackbar-error',
					duration: 5000,
				});
			}

			responseService.setStatusCode(404);
			adsState.setAd(undefined);
			return of(null);
		}),
		tap((ad: Ad) => {
			let ad_title = '';

			if (ad?.title) {
				const AD_URL = ad?.adlink.split('/');
				ad_title = AD_URL[AD_URL.length - 1];
			}

			if (route?.params?.['title'] !== ad_title) {
				responseService.setStatusCode(404);

				adsState.setAd(undefined);
			} else {
				adsState.setAd(ad);
			}
		}),
		finalize(() => adsState.setIsLoading(false))
	);
};
